.catalog {
  padding: 85px 0 120px;

  @include vp-1023 {
    padding: 90px 0 100px;
  }

  @include vp-767 {
    padding: 57px 0 65px;
  }
}

.catalog h2 {
  font-weight: 600;
  font-size: 28px;
  line-height: 150%;
  color: $color-tangaroa;
  margin: 0 0 50px;
  text-align: center;

  @include vp-1023 {
    margin-bottom: 48px;
    font-size: 26px;
  }

  @include vp-767 {
    font-size: 24px;
    margin-bottom: 31px;
  }
}

.catalog__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  list-style: none;
  margin: 0;
  padding: 0 15px;

  @include vp-1023 {
    grid-template-columns: repeat(2, 1fr);
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    padding: 0 10px;
  }
}
