@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  min-width: 320px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.header {
  position: relative;
  width: 100%;
  color: #f9fbfd;
  background-color: #011c40;
  background-image: image-set(url("../img/bg-img/bg-header-desc.webp") 1x, url("../img/bg-img/bg-header-desc.jpg") 1x);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .header {
    background-image: image-set(url("../img/bg-img/bg-header-desc@2x.webp") 2x, url("../img/bg-img/bg-header-desc@2x.jpg") 2x);
  }
}
@media (max-width: 1023px) {
  .header {
    background-image: image-set(url("../img/bg-img/bg-header-tabl.webp") 1x, url("../img/bg-img/bg-header-tabl.jpg") 1x);
  }
}
@media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
  .header {
    background-image: image-set(url("../img/bg-img/bg-header-tabl@2x.webp") 2x, url("../img/bg-img/bg-header-tabl@2x.jpg") 2x);
  }
}
@media (max-width: 767px) {
  .header {
    background-image: image-set(url("../img/bg-img/bg-header-mob.webp") 1x, url("../img/bg-img/bg-header-mob.jpg") 1x);
  }
}
@media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
  .header {
    background-image: image-set(url("../img/bg-img/bg-header-mob@2x.webp") 2x, url("../img/bg-img/bg-header-mob@2x.jpg") 2x);
  }
}

.header h1 {
  margin: 0;
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .header h1 {
    font-size: 50px;
    line-height: 61px;
    text-shadow: none;
  }
}
@media (max-width: 767px) {
  .header h1 {
    font-size: 36px;
    line-height: 45px;
  }
}

.header p {
  margin: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .header p {
    font-size: 18px;
    line-height: 22px;
    text-shadow: none;
  }
}
@media (max-width: 767px) {
  .header p {
    font-size: 16px;
  }
}

.header__container {
  min-height: 650px;
}
@media (max-width: 1023px) {
  .header__container {
    min-height: 638px;
  }
}
@media (max-width: 767px) {
  .header__container {
    min-height: 500px;
  }
}

.header__wrapper {
  padding: 33px 0;
}
@media (max-width: 1023px) {
  .header__wrapper {
    padding: 28px 0;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    padding: 21px 16px;
  }
}

.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__burger {
  display: none;
}
@media (max-width: 767px) {
  .header__burger {
    position: relative;
    display: block;
    width: 24px;
    height: 14px;
    padding: 0;
    border: none;
    background-color: transparent;
    border-top: 2px solid #f9fbfd;
    cursor: pointer;
  }
}
@media (max-width: 767px) and (hover: hover) {
  .header__burger:hover:not(.focus-visible) {
    opacity: 0.5;
  }
}
@media (max-width: 767px) {
  .header__burger.focus-visible:focus {
    opacity: 0.5;
  }
}
@media (max-width: 767px) {
  .header__burger::before, .header__burger::after {
    content: "";
    position: absolute;
    left: 50%;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
    transform: translateX(-50%);
  }
}
@media (max-width: 767px) {
  .header__burger::before {
    bottom: 0;
  }
}
@media (max-width: 767px) {
  .header__burger::after {
    top: calc(50% - 1px);
    transform: translate(-50%, -50%);
  }
}

.header__content {
  padding: 166px 95px;
}
@media (max-width: 1023px) {
  .header__content {
    padding: 188px 0;
  }
}
@media (max-width: 767px) {
  .header__content {
    padding: 176px 0;
  }
}

@media (max-width: 767px) {
  .is-open {
    min-height: 100vh;
    padding: 0 0 0 35px;
    background-color: rgba(0, 43, 102, 0.5);
    inset: 0;
  }
}

@media (max-width: 767px) {
  .is-open .header__nav,
.no-js .header__nav {
    display: grid;
    grid-template-columns: repeat(2, auto);
    justify-content: space-between;
    align-content: start;
    height: 100%;
    gap: 42px 10px;
    background-color: #f9fbfd;
    padding: 21px 15px;
  }
}

@media (max-width: 767px) {
  .is-open .header__logo svg,
.no-js .header__logo svg {
    fill: #011c40;
  }
}

@media (max-width: 767px) {
  .is-open .header__burger {
    border-color: transparent;
    justify-self: end;
  }
}
@media (max-width: 767px) {
  .is-open .header__burger::before, .is-open .header__burger::after {
    top: 50%;
    right: 5px;
    left: 8px;
    width: 20px;
    background-color: #011c40;
  }
}
@media (max-width: 767px) {
  .is-open .header__burger::before {
    transform: rotate(-45deg);
  }
}
@media (max-width: 767px) {
  .is-open .header__burger::after {
    transform: rotate(45deg);
  }
}

@media (max-width: 767px) {
  .is-open .header__site-menu,
.no-js .header__site-menu {
    display: block;
    grid-column: 1/-1;
    width: 100%;
    height: 100%;
    padding-left: 10px;
    overflow: auto;
    justify-self: start;
  }
}

@media (max-width: 767px) {
  .is-open .header__site-menu a {
    color: #011c40;
  }
}

@media (max-width: 767px) {
  .is-open li:not(:last-child) {
    margin-bottom: 27px;
  }
}

@media (max-width: 767px) {
  .no-js .header__nav {
    position: static;
    margin: 0 -15px;
  }
}

.no-js .header__burger {
  display: none;
}

.logo {
  display: block;
  width: 70px;
  height: 38px;
  color: #f9fbfd;
