  font-size: 28px;
  line-height: 150%;
  margin: 0;
  margin-bottom: 50px;
  text-align: center;
}
@media (max-width: 1023px) {
  .advantages h2 {
    font-size: 26px;
  }
}
@media (max-width: 767px) {
  .advantages h2 {
    font-size: 24px;
    margin-bottom: 30px;
  }
}

.advantages ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
}
@media (max-width: 1023px) {
  .advantages ul {
    gap: 26px;
  }
}
@media (max-width: 767px) {
  .advantages ul {
    grid-template-columns: 1fr;
    gap: 31px;
  }
}

.advantages h3 {
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  margin: 0;
  margin-bottom: 10px;
}
@media (max-width: 1023px) {
  .advantages h3 {
    line-height: 140%;
  }
}
@media (max-width: 767px) {
  .advantages h3 {
    max-width: 200px;
  }
}

.advantages p {
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  margin: 0;
}

.advantages__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.advantages__item {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #f9fbfd;
  padding: 80px 30px 45px;
}
@media (max-width: 1023px) {
  .advantages__item {
    padding: 70px 20px 22px;
  }
}
@media (max-width: 767px) {
  .advantages__item {
    padding: 71px 18px 19px;
  }
}
.advantages__item::before {
  content: "";
  position: absolute;
  top: 30px;
  left: 30px;
  width: 44px;
  height: 32px;
  background-repeat: no-repeat;
  background-size: contain;
}
@media (max-width: 1023px) {
  .advantages__item::before {
    top: 26px;
    left: 20px;
    width: 36px;
    height: 26px;
  }
}
@media (max-width: 767px) {
  .advantages__item::before {
    top: 28px;
  }
}
.advantages__item--liner::before {
  background-image: url("../img/svg/icon-liner.svg");
}
.advantages__item--all-inclusive::before {
  background-image: url("../img/svg/icon-all-inclusive.svg");
}
@media (max-width: 767px) {
  .advantages__item--all-inclusive::before {
    top: 25px;
    left: 18px;
  }
}
.advantages__item--zodiac::before {
  background-image: url("../img/svg/icon-zodiac.svg");
}
@media (max-width: 767px) {
  .advantages__item--zodiac::before {
    top: 21px;
  }
}

.catalog {
