.card {
  position: relative;
  color: #f9fbfd;
  background: linear-gradient(180deg, rgba(1, 28, 64, 0) 50%, #011c40 100%);
  cursor: pointer;
}

.card img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.card h3 {
  position: absolute;
  right: 0;
  bottom: 24px;
  left: 0;
  margin: 0 30px;
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
}
@media (max-width: 1023px) {
  .card h3 {
    bottom: 27px;
    margin: 0 35px;
  }
}
@media (max-width: 767px) {
  .card h3 {
    bottom: 17px;
    margin: 0 25px;
  }
}

.card__content {
  position: absolute;
  z-index: -1;
  display: grid;
  grid-template-rows: auto 1fr auto;
  align-content: start;
  padding: 28px 25px 34px;
  color: #d8d8d9;
  background-color: #011c40;
  opacity: 0;
  transition: opacity 0.3s ease-in;
  inset: 0;
  pointer-events: none;
}
@media (max-width: 1023px) {
  .card__content {
    padding: 28px 35px 34px;
  }
}
@media (max-width: 767px) {
  .card__content {
    padding: 28px 25px 34px;
  }
}

.card p {
  margin: 0 0 14px;
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
}
@media (max-width: 1023px) {
  .card p {
    margin-bottom: 16px;
  }
}

.card dl {
  margin: 0 0 4px;
  overflow: auto;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  scrollbar-color: #ffffff #808080;
  scrollbar-width: auto;
}
.card dl::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
.card dl::-webkit-scrollbar-track {
  background-color: #808080;
}
.card dl::-webkit-scrollbar-thumb {
  background-color: #ffffff;
  border: 2px solid #808080;
  border-radius: 20px;
}

.card dt {
  font-weight: 500;
}

.card dd {
  margin: 0 0 13px;
}

.card:focus-within .card__content,
.card:hover .card__content {
  z-index: 1;
  box-shadow: 0 4px 35px rgba(1, 28, 64, 0.15);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.button {
  display: block;
  width: 100%;
  padding: 10px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #011c40;
  text-decoration: none;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
  user-select: none;
}
@media (hover: hover) {
  .button:hover:not(.focus-visible) {
    color: #ffffff;
    background-color: #0059cd;
    outline: none;
  }
}
.button.focus-visible:focus {
  color: #ffffff;
  background-color: #0059cd;
  outline: none;
}
.button:active {
  opacity: 0.5;
}
.button:disabled, .button--disabled {
  color: #b1b1b1;
  background-color: #c6c6c6;
}

